import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, useRouteMatch } from 'react-router-dom';

ProductManager.propTypes = {

};

function ProductManager(props) {

    const match = useRouteMatch()
    const MainPage = React.lazy(() => import('./pages/MainPage'))
    const AddPage = React.lazy(() => import('./pages/AddPage'))
    const EditPage = React.lazy(() => import('./pages/EditPage'))
    const ViewPage = React.lazy(() => import('./pages/ViewPage'))
    return (
        <Switch>
            <Route exact path={match.url} component={MainPage} />
            <Route path={`${match.url}/add`} component={AddPage} />
            <Route path={`${match.url}/edit/:productId`} component={EditPage} />
            <Route path={`${match.url}/:productId`} component={ViewPage} />
        </Switch>
    );
}

export default ProductManager;